<template>
  <div
    class="c-collect-ticket l-page u-background--blue"
  >
    <v-nav
      :title="$t('ticket.collect.title')"
      custom
      :icon="navIcon"
      @back="toBack"
    />
    <template v-if="step > 0">
      <div class="l-page__content c-result-wrapper">
        <div
          class="c-result"
          :class="{ 'has-status': step === 2 }"
        >
          <div
            v-if="step === 1"
            class="c-result__header"
          >
            {{ $t('ticket.collect.tip') }}
          </div>
          <div
            v-if="step === 2"
            class="c-result__header"
          >
            <div class="c-result__status">
              <i
                class="c-result__icon"
                :style="avatar"
              ></i>
            </div>
            {{ $t('ticket.collect.success') }}
          </div>
          <div
            v-if="step === 3 && message"
            class="c-result__header"
          >
            {{ message }}
          </div>
          <div class="c-result__content">
            <div class="is-title--page is-ellipsis--multi">{{ ticket.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ ticket.valid }}</div>
            <div
              v-if="step === 1"
              class="u-separate--far l-flex--row"
            >
              <div
                class="o-button is-trigger is-hollow is-auto"
                @click="dialogMsg.show = true"
              >
                {{ $t('btn.cancelupper') }}
              </div>
              <div
                class="o-button is-trigger is-auto"
                @click="toReslove(2)"
              >
                {{ $t('btn.confirmupper') }}
              </div>
            </div>
            <div
              v-if="step === 2"
              class="u-separate--far"
            >
              <div class="c-vlist-item--near">
                <router-link
                  tag="div"
                  class="o-button is-trigger is-auto"
                  :to="{ name: 'escheduler' }"
                  replace
                >
                  {{ $t('common.payment.result.escheduler') }}
                </router-link>
              </div>
              <div class="u-separate--far u-wrapper">
                <router-link
                  tag="div"
                  class="u-inline-flex"
                  :to="{ name: 'mine' }"
                  replace
                >
                  <div class="is-sub--blue">{{ $t('common.payment.result.view') }}</div>
                  <img class="o-icon" src="@/assets/icon-arrow--right--blue.png"/>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('ticket.collect.askt')"
      :message="$t('ticket.collect.ask')"
      :confirm-button-text="$t('btn.confirm')"
      :cancel-button-text="$t('btn.cancel')"
      show-cancel-button
      @confirm="toReslove(3)"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { transformTicketValidTime } from '@/utils/format.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Icon } from 'vant'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Icon.name ]: Icon
  },
  data () {
    return {
      step: 0,
      message: '',
      ticket: null
    }
  },
  computed: {
    ...mapGetters(['avatarUrl']),
    navIcon () {
      return this.step > 1 ? 'cross' : 'down'
    },
    avatar () {
      return this.avatarUrl ? { backgroundImage: `url("${ this.avatarUrl }")` } : null
    }
  },
  methods: {
    toReslove (status) {
      this.$http.post('/api/common/user/ticket/share/process', {
        shareId: this.$id,
        status
      }, {
        login: true,
        loading: true,
        server: true
      }).then(() => {
        if (status === 2) {
          this.step = 2
        } else {
          this.toBack()
        }
      })
    },
    toBack () {
      this.$features.tabbar('home')
    }
  },
  created () {
    this.$id = Number(this.$route.params.id)
    this.$http.post('/api/common/user/ticket/share/detail', {
      shareId: this.$route.params.id
    }, {
      login: true,
      server: true
    }).then(data => {
      if (data.canAccept && !data.isAccept) {
        this.ticket = {
          name: data.ticketName,
          valid: transformTicketValidTime(data)
        }
        this.step = 1
      } else {
        this.step = 3
        this.message = data.isAccept ? data.isSelfAccept ? this.$t('ticket.collect.accepts') : this.$t('ticket.collect.accepto') : ''
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.c-collect-ticket {
  /deep/ .van-dialog.c-dialog {
    .van-dialog__header, .van-dialog__message {
      color: $color-black;
    }
  }
}
</style>
