<template>
  <div class="l-page" @touchmove="clean" @click="clean">
    <v-nav :title="$t('escheduler.title')"/>
    <div class="l-flex__fill l-flex--column">
      <div class="l-flex--row has-shadow--short u-background--blue">
        <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="switchTab(0)">{{ $t('escheduler.application') }}</div>
        <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="switchTab(1)">{{ $t('escheduler.result') }}</div>
      </div>
      <div class="l-hscroll l-flex__fill">
        <div class="l-hscroll__wrapper" :class="{ 'is-second': tabIndex === 1 }">
          <div class="l-hscroll__item l-flex--column">
            <div class="l-flex__fill u-scroll-y">
              <div class="c-escheduler-ticket-list l-flex__none u-relative">
                <div class="c-escheduler-ticket-list__title">
                  <div class="l-flex--row">
                    <div class="is-tip--dark l-flex__fill">{{ $t('escheduler.cticket') }}</div>
                    <div class="is-mini--blue u-font l-flex__none">{{ count }}{{ $t('escheduler.chosen') }}</div>
                  </div>
                  <div class="is-mini--dark u-font u-separate--nearest l-flex__none">{{ $t('escheduler.help') }}</div>
                </div>
                <div class="l-hlist l-flex__none">
                  <div class="c-escheduler-ticket-list__content l-hlist__wrapper">
                    <ticket
                      v-for="ticket in tickets"
                      :key="ticket.origin.id"
                      class="c-escheduler-ticket-list__item"
                      :ticket="ticket"
                      @click.native="toggle(ticket)"
                      @info="showInfo"
                    />
                  </div>
                </div>
                <div
                  v-if="tabIndex === 0 && popInfo"
                  class="c-pop-info has-shadow"
                  @click.stop
                  @touchmove.stop
                >
                  <van-icon
                    class="c-pop-info__close"
                    name="cross"
                    @click="clean"
                  />
                  <div class="is-content--dark">{{ popInfo.name }}</div>
                  <div class="is-tip--dark u-font u-separate--nearest">{{ popInfo.tip }}</div>
                  <div class="is-tip u-separate--nearest">{{ popInfo.time }}</div>
                </div>
              </div>
              <div
                v-if="hasAttr"
                class="c-attr-list has-separate--t"
              >
                <div class="c-attr-list__title">
                  <div class="l-flex--row">
                    <div class="is-tip--dark l-flex__fill">{{ $t('escheduler.cattr') }}</div>
                    <div class="is-tip--dark u-font l-flex__none">{{ attrCount }}/{{ maxEschedulerCount }}</div>
                  </div>
                </div>
                <div class="is-mini--dark u-font u-separate--nearest l-flex__none">{{ $t('escheduler.cattrh') }}</div>
                <div class="u-separate--nearest">
                  <div
                    v-for="attr in attrs"
                    :key="attr.rnaId"
                    class="c-attr has-hairline--b l-flex--row"
                    :class="{ 'is-disable': attr.disable }"
                     @click="toggleAttr(attr)"
                  >
                    <v-resource
                      class="c-attr__img l-flex__nonw"
                      :info="attr.picInfo"
                    />
                    <div class="l-flex__fill u-h-separate--far">
                      <div class="c-attr__text is-content--dark">{{ attr.name }}</div>
                      <div class="c-attr__text is-mini--orange u-font--bold u-separate--nearest">{{ $t('escheduler.quota', {
                        count: attr.restCount
                      }) }}</div>
                    </div>
                    <van-icon
                      class="c-attr__selected is-circular u-h-separate--farthest l-flex__none"
                      :class="{ 'is-selected': attr.choose }"
                      name="success"
                    />
                  </div>
                </div>
              </div>
              <div class="has-padding--around has-separate--t">
                <div class="is-tip--dark">{{ $t('escheduler.appdesc') }}</div>
                <div class="is-content--dark u-font u-separate--nearer">{{ $t('escheduler.eapp') }}</div>
                <ol class="c-ol is-content--dark u-font u-separate--nearest">
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.eapp1') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.eapp2') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.eapp3') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.eapp4') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.eapp5') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.eapp6') }}</li>
                </ol>
                <div class="is-content--dark u-font u-separate">{{ $t('escheduler.aapp') }}</div>
                <ol class="c-ol is-content--dark u-font u-separate--nearest">
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.aapp1') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.aapp2') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.aapp3') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.aapp4') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.aapp5') }}</li>
                  <li class="c-vlist-item--nearest">{{ $t('escheduler.aapp6') }}</li>
                </ol>
              </div>
            </div>
            <div class="c-fixed-footer has-hairline--tb">
              <v-button
                class="l-flex__fill"
                :text="$t('btn.confirmupper')"
                :disable="disable"
                small
                @click="toApply"
              />
            </div>
          </div>
          <div class="l-hscroll__item u-scroll-y">
            <template v-if="hasPassed">
              <div class="c-escheduler-ticket-list l-flex__none u-relative">
                <div class="c-escheduler-ticket-list__title is-tip--dark">{{ $t('escheduler.aticket') }}</div>
                <div class="l-hlist l-flex__none">
                  <div class="c-escheduler-ticket-list__content l-hlist__wrapper">
                    <ticket
                      v-for="(ticket, index) in passedTickets"
                      :key="ticket.origin.id"
                      class="c-escheduler-ticket-list__item"
                      :ticket="ticket"
                      @click.native="chooseTqTicket(index)"
                      @info="showInfo"
                    />
                  </div>
                </div>
                <div
                  v-if="tabIndex === 1 && popInfo"
                  class="c-pop-info c-pop-info--short has-shadow"
                  @click.stop
                  @touchmove.stop
                >
                  <van-icon
                    class="c-pop-info__close"
                    name="cross"
                    @click="clean"
                  />
                  <div class="is-content--dark">{{ popInfo.name }}</div>
                  <div class="is-tip--dark u-font u-separate--nearest">{{ popInfo.tip }}</div>
                  <div class="is-tip u-separate--nearest">{{ popInfo.time }}</div>
                </div>
              </div>
              <div class="c-attr-list has-separate--t">
                <div class="c-attr-list__title is-tip--dark">{{ $t('escheduler.priviledge') }}</div>
                <div class="u-separate--nearest">
                  <div
                    v-for="attr in tqAttes"
                    :key="attr.rnaId"
                    class="c-attr has-hairline--b l-flex--row"
                     @click="showAttr(attr)"
                  >
                    <v-resource
                      class="c-attr__img l-flex__nonw"
                      :info="attr.picInfo"
                    />
                    <div class="l-flex__fill u-h-separate--far">
                      <div class="c-attr__text is-content--dark">{{ attr.name }}</div>
                      <div class="c-attr__text is-mini--orange u-font--bold u-separate--nearest">{{ attr.time }}</div>
                    </div>
                    <van-icon
                      class="c-attr__arrow u-h-separate--farthest l-flex__none"
                      name="arrow"
                    />
                  </div>
                </div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :message="dialogMsg.content"
      :confirm-button-text="$t('btn.ok')"
      @close="closeDialog"
    />
    <van-popup
      v-if="attrInfo.showList"
      class="l-flex--column"
      v-model="attrInfo.show"
      position="bottom"
      round
      @closed="attrInfo.showList = false"
    >
      <div class="c-close-header has-hairline--b">
        {{ $t('escheduler.adetail') }}
        <van-icon
          class="c-close-header__close"
          name="cross"
          @click="attrInfo.show = false"
        />
      </div>
      <div class="c-application-detail u-scroll-y">
        <div class="has-padding--h">
          <div class="has-padding--v has-hairline--b">
            <div class="is-tip--dark">{{ $t('escheduler.cnticket') }}</div>
            <div class="is-tip--dark u-font u-separate--nearest">{{ attrInfo.ticket }}</div>
            <div class="is-tip--dark u-separate--near">{{ $t('escheduler.cnattr') }}</div>
            <div class="is-tip--dark u-font u-separate--nearest">{{ attrInfo.attr }}</div>
          </div>
        </div>
        <div class="c-application-detail__time is-tip--dark">{{ attrInfo.isUsed ? $t('escheduler.cleave') : $t('escheduler.carrive') }}</div>
        <div class="l-hlist">
          <div class="l-hlist__wrapper">
            <div
              v-for="(time, index) in attrInfo.timeList"
              :key="index"
              class="c-hlist-item--near o-option"
              :class="{ 'is-selected': index === attrInfo.index }"
              @click="attrInfo.index = index"
            >
              {{ time }}
            </div>
          </div>
        </div>
        <div class="c-application-detail__note is-tip--dark u-font">{{ attrInfo.isUsed ? $t('escheduler.cleaveh') : $t('escheduler.carriveh') }}</div>
      </div>
      <div class="c-fixed-footer has-hairline--tb">
        <div class="o-button is-trigger is-auto" @click="toSubmit">{{ $t('btn.submit') }}</div>
      </div>
    </van-popup>
    <van-popup
      v-if="tq.showList"
      v-model="tq.show"
      class="c-tq"
      @closed="tq.showList = false"
    >
      <div class="u-relative">
        <div class="c-tq__content">
          <div class="c-tq__header has-hairline--b">
            <div class="is-title is-ellipsis--multi">{{ tq.attr.name }}</div>
            <div class="is-content--orange u-separate--nearer">{{ tq.attr.time }}</div>
          </div>
          <v-resource
            class="c-tq__qr u-separate--nearer"
            :info="tq.attr.qr"
          />
        </div>
        <van-icon
          class="c-tq__button is-circular u-inline-flex u-separate--farther"
          name="cross"
          @click="tq.show = false"
        />
      </div>
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { transformTicketValidTime, dateFormat } from '@/utils/format.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Icon, Popup } from 'vant'
import VButton from '@/components/VButton.vue'
import Ticket from '@/components/escheduler/Ticket.vue'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Icon.name ]: Icon,
    [ Popup.name ]: Popup,
    Ticket,
    VButton
  },
  props: {
    esTickets: {
      type: Array,
      default () {
        return []
      }
    },
    timeList: {
      type: Array,
      default () {
        return null
      }
    }
  },
  data () {
    return {
      tabIndex: 0,
      tickets: [],
      attrs: [],
      popInfo: null,
      tqIndex: -1,
      tq: {
        showList: false,
        show: false,
        attr: null
      },
      passedTickets: [],
      attrInfo: {
        showList: false,
        show: false,
        index: 0,
        timeList: []
      }
    }
  },
  computed: {
    ...mapState(['config']),
    maxEschedulerCount () {
      return this.config.maxEschedulerCount
    },
    orderTimeList () {
      return this.timeList || this.config.orderTimeList || []
    },
    afterOrderTimeList () {
      return this.config.afterOrderTimeList || []
    },
    chooseTickets () {
      return this.tickets.filter(ticket => ticket.choose)
    },
    count () {
      return this.chooseTickets.length
    },
    hasAttr () {
      return this.attrs.length > 0
    },
    chooseAttrs () {
      return this.attrs.filter(ticket => ticket.choose)
    },
    attrCount () {
      return this.chooseAttrs.length
    },
    disable () {
      return this.count === 0
    },
    hasPassed () {
      return this.passedTickets.length > 0
    },
    tqAttes () {
      return this.hasPassed ? this.passedTickets[this.tqIndex].attrs : []
    }
  },
  methods: {
    switchTab (index) {
      this.tabIndex = index
      if (this.tabIndex === 1) {
        this.$http.post('/api/common/user/ticket/escheduler/successResultList', null, {
          login: true,
          loading: true,
          server: true
        }).then(data => {
          this.passedTickets = data.data.map(this.transformTq)
          if (this.hasPassed) {
            this.chooseTqTicket(0)
          }
        }, err => !err.abort && this.switchTab(0))
      } else {
        this.tqIndex = -1
        this.passedTickets = []
      }
    },
    showAttr (attr) {
      this.tq.attr = attr
      this.tq.showList = true
      this.$nextTick(() => this.tq.show = true)
    },
    chooseTqTicket (index) {
      if (this.tqIndex > -1) {
        this.passedTickets[this.tqIndex].choose = false
      }
      this.tqIndex = index
      this.passedTickets[index].choose = true
    },
    transformTq (ticket) {
      return {
        origin: {
          id: `tq${ ticket.userTicketId }`,
          name: ticket.productName,
          tip: ticket.ticketName,
          time: transformTicketValidTime(ticket)
        },
        attrs: ticket.eschedulerList.map(attr => {
          return {
            rnaId: attr.rnaId,
            name: attr.rnaName,
            qr: attr.pairedCodePicInfo,
            picInfo: attr.picInfo,
            time: attr.timeSlot
          }
        }),
        msg: dateFormat(ticket.eschedulerTime, 'yyyy-MM-dd'),
        choose: false
      }
    },
    transformTicket ({ origin, msg }) {
      return {
        origin, msg,
        choose: false
      }
    },
    showInfo (ticketOrigin) {
      if (!this.popInfo || this.popInfo.id !== ticketOrigin.id) {
        this.popInfo = {
          id: ticketOrigin.id,
          name: ticketOrigin.name,
          tip: ticketOrigin.tip,
          time: ticketOrigin.time
        }
      }
    },
    clean () {
      this.popInfo = null
    },
    sortByDisable (a, b) {
      return a.disable === b.disable ? 0 : b.disable ? -1 : 1
    },
    transformAttr (attr) {
      return {
        choose: false,
        disable: attr.restCount < this.count,
        ...attr
      }
    },
    refreshAttrs () {
      let hasChanged = false
      this.attrs.forEach(attr => {
        let disable = attr.restCount < this.count
        if (attr.disable !== disable) {
          hasChanged = true
          attr.disable = disable
          if (attr.choose && attr.disable) {
            attr.choose = false
          }
        }
      })
      hasChanged && this.attrs.sort(this.sortByDisable)
    },
    getTimestamp (ticket) {
      let time = (ticket.isUsed ? this.afterOrderTimeList[0] : this.orderTimeList[0])
      return new Date(`${ ticket.msg.replace(/-/g, '/') } ${ time ? time.slice(0, 5) : '00:00' }:00`).getTime()
    },
    getAttrs (target) {
      let ticket = target || this.chooseTickets[0]
      this.$http.post('/api/common/user/ticket/escheduler/rna/list', {
        odTicketIdList: [ticket.origin.id],
        orderTime: this.getTimestamp(ticket)
      }, {
        login: true,
        loading: true,
        server: true
      }).then(data => {
        if (target) {
          target.choose = true
        }
        this.attrs = data.rnaList.map(this.transformAttr).sort(this.sortByDisable)
      })
    },
    toggleAttr (attr) {
      if (attr.choose) {
        attr.choose = false
      } else {
        if (attr.disable) {
          return
        }

        if (this.chooseAttrs.length === this.maxEschedulerCount) {
          this.$toast(this.$t('escheduler.errMsg.max', { count: this.maxEschedulerCount }))
        } else {
          attr.choose = true
        }
      }
    },
    toggle (ticket) {
      if (ticket.choose) {
        ticket.choose = false
        if (this.count === 0) {
          this.attrs = []
        } else {
          this.refreshAttrs()
        }
      } else {
        if (this.count === 0) {
          this.getAttrs(ticket)
        } else {
          if (this.chooseTickets[0].msg !== ticket.msg) {
            this.$toast(this.$t('escheduler.errMsg.same'))
          } else {
            ticket.choose = true
            this.refreshAttrs()
          }
        }
      }
    },
    showApplyInfo (orderTimeList) {
      this.attrInfo.isUsed = this.chooseTickets[0].isUsed
      this.attrInfo.index = 0
      this.attrInfo.timeList = orderTimeList
      this.attrInfo.ticket = this.chooseTickets.map(ticket => ticket.origin.name).join(', ')
      this.attrInfo.attr = this.chooseAttrs.map(attr => attr.name).join(', ')
      this.attrInfo.showList = true
      this.$nextTick(() => this.attrInfo.show = true)
    },
    toApply () {
      if (this.chooseTickets[0].isUsed) {
        this.$http.post('/api/common/user/ticket/escheduler/timeList', {
          odTicketId: this.chooseTickets[0].origin.id
        }, {
          login: true,
          loading: true,
          server: true
        }).then(data => {
          if (!data.timeList || data.timeList.length === 0) {
            this.$toast(this.$t('escheduler.errMsg.overtime'))
          } else {
            this.showApplyInfo(data.timeList)
          }
        })
      } else {
        this.showApplyInfo(this.orderTimeList)
      }
    },
    closeDialog () {
      if (this.$success) {
        this.$features.back()
      }
    },
    toSubmit () {
      if (!this.attrInfo.timeList[this.attrInfo.index]) {
        return
      }

      let raList = this.chooseAttrs.map((attr, index) => {
        return {
          rnaId: attr.rnaId,
          sortIndex: index + 1
        }
      })
      this.$http.post('/api/common/user/ticket/escheduler/submit', {
        odTicketIdList: this.chooseTickets.map(ticket => {
          return {
            odTicketId: ticket.origin.id,
            raList
          }
        }),
        orderTime: new Date(`${ this.chooseTickets[0].msg.replace(/-/g, '/') } ${ this.attrInfo.timeList[this.attrInfo.index].slice(0, 5) }:00`).getTime()
      }, {
        login: true,
        loading: true
      }).then(() => {
        this.dialogMsg.content = this.$t('escheduler.successMsg')
        this.dialogMsg.show = true
        this.$success = true
      }, err => {
        if (!err.abort) {
          this.dialogMsg.content = this.$t('escheduler.failMsg', { msg: err.msg })
          this.dialogMsg.show = true
        }
      }).finally(() => this.attrInfo.show = false)
    }
  },
  created () {
    if (this.esTickets && this.esTickets.length > 0) {
      this.tickets = this.esTickets.map(this.transformTicket)
      this.toggle(this.tickets[0])
    }
  }
}
</script>

<style lang="scss" scoped>
.c-escheduler-ticket-list {
  &__title {
    padding: 40px 40px 0;
  }

  &__content {
    padding: 32px 40px 40px;
  }

  &__item {
    width: 326px;

    & + & {
      margin-left: 20px;
    }
  }
}

.c-attr-list {
  padding: 40px 0 15px 40px;

  &__title {
    padding-right: 40px;
  }
}

.c-attr {
  padding: 32px 48px 32px 0;

  &.is-disable {
    .c-attr__img {
      opacity: .2;
    }

    .c-attr__text {
      color: $color;
    }

    .c-attr__selected {
      visibility: hidden;
    }
  }

  &__img {
    width: 160px;
    height: 160px;
    border-radius: 16px;
    overflow: hidden;
  }

  &__selected {
    padding: 8px;
    font-size: 32px;
    color: $color-white;
    border: 2px solid $color-separate;

    &.is-selected {
      border: none;
      background-color: $color-blue;
    }
  }

  &__arrow {
    font-size: 32px;
    color: $color;
  }
}

.c-pop-info {
  position: absolute;
  top: 200px;
  left: 50%;
  width: 472px;
  padding: 32px;
  border-radius: 16px;
  background-color: $color-white;
  z-index: 99;
  transform: translate(-50%, 0);

  &.c-pop-info--short {
    top: 160px;
  }

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px;
    font-size: 36px;
    color: $color;
  }
}

.c-ol {
  padding-left: 28px;
  list-style-type: decimal;
}

.c-application-detail {
  &__time {
    padding: 32px 40px 24px 40px;
  }

  &__note {
    padding: 32px 40px 48px;
  }
}

.c-tq {
  width: 560px;
  text-align: center;
  background-color: transparent;

  &__content {
    border-radius: 16px;
    background-color: $color-white;
  }

  &__header {
    padding: 48px;
  }

  &__qr {
    width: 388px;
    height: 388px;
    padding: 60px 0 82px;
  }

  &__button {
    width: 124px;
    height: 124px;
    font-size: 42px;
    color: $color-white;
    background-color: rgba($color-white, .3);
  }
}
</style>
