<template>
  <div class="o-cell u-font--bold has-hairline--b l-flex--row" @click="onClick">
    <div class="is-content--dark l-flex__fill">{{ title }}</div>
    <div class="l-flex__none l-flex--row">
      <div class="is-tip">{{ value }}</div>
      <van-switch
        v-if="toggle"
        :value="checked"
        class="is-fixed-r"
        active-color="#00A9E0"
        inactive-color="#DDDDDD"
        @change="onSwitchChange"
      />
      <img
        v-if="arrow"
        class="o-icon"
        src="@/assets/icon-arrow--right.png"
      />
      <slot/>
    </div>
  </div>
</template>

<script>
import { Switch } from 'vant'

export default {
  components: {
    [ Switch.name ]: Switch
  },
  props: {
    title: {
      // 
      type: String,
      default: ''
    },
    value: {
      // 
      type: String,
      default: ''
    },
    arrow: {
      // 
      type: [Boolean, String],
      default: false
    },
    toggle: {
      // Switch
      type: [Boolean, String],
      default: false
    },
    checked: {
      // Switch
      type: [Boolean, String],
      default: false
    }
  },
  computed: {
    hasValue () {
      return !!this.value
    },
    isVertical () {
      return this.hasValue && this.vertical
    }
  },
  methods: {
    onClick () {
      this.$emit('click')
    },
    onSwitchChange (checked) {
      this.$emit('change', checked)
    }
  }
}
</script>

<style lang="scss" scoped>
.o-cell {
  position: relative;
  height: 120px;
}
</style>
