<template>
  <div class="l-page" @touchmove="clean" @click="clean">
    <v-nav :title="$t('escheduler.title')"/>
    <div class="l-page__content l-flex--column">
      <template v-if="hasTicket">
        <router-link
          v-if="canChooseDate"
          tag="div"
          class="has-padding--around l-flex--row l-flex__none"
          :to="{ name: 'escheduler-date' }"
        >
          <div class="l-flex__fill is-tip--dark">{{ $t('escheduler.tip') }}</div>
          <div class="u-inline-flex l-flex__none">
            <div class="is-sub--blue">{{ $t('escheduler.select') }}</div>
            <img class="o-icon" src="@/assets/icon-arrow--right--blue.png"/>
          </div>
        </router-link>
        <div class="l-flex__fill u-scroll-y">
          <div class="c-list--2c has-separate--t has-padding--around">
            <ticket
              v-for="ticket in tickets"
              :key="ticket.origin.id"
              :ref="ticket.origin.ref"
              class="c-list--2c__item is-near"
              :ticket="ticket"
              @click.native="toggle(ticket)"
              @info="showInfo"
            />
          </div>
        </div>
        <div class="c-fixed-footer has-hairline--tb">
          <v-button
            class="l-flex__fill"
            :text="$t('btn.nextstep')"
            :disable="disable"
            small
            @click="toNext"
          />
        </div>
      </template>
      <div v-else class="c-tip is-fill">
        <img class="c-tip__img" src="~@/assets/no-ticket.png"/>
        <div class="c-tip__tip">{{ $t('escheduler.noticket') }}</div>
        <div class="c-tip__footer">
          <router-link
            tag="div"
            class="c-tip__button"
            :to="{ name: 'ticket-buy' }"
          >
            {{ $t('btn.buyticket') }}
          </router-link>
          <router-link
            tag="div"
            class="c-tip__button"
            :to="{ name: 'ticket-bind' }"
          >
            {{ $t('btn.bindticket') }}
          </router-link>
        </div>
      </div>
    </div>
    <div
      v-if="popInfo"
      class="c-pop-info has-shadow"
      :class="{ 'is-top': popInfo.isTop }"
      :style="{ top: popInfo.top, left: popInfo.left }"
      @click.stop
      @touchmove.stop
    >
      <van-icon
        class="c-pop-info__close"
        name="cross"
        @click="clean"
      />
      <div class="is-content--dark">{{ popInfo.name }}</div>
      <div class="is-tip--dark u-font u-separate--nearest">{{ popInfo.tip }}</div>
      <div class="is-tip u-separate--nearest">{{ popInfo.time }}</div>
    </div>
    <transition name="slide-wrapper">
      <router-view style="z-index: 9;" :time-list="timeList" :es-tickets="chooseTickets" :date-tickets="dateTickets"/>
    </transition>
  </div>
</template>

<script>
import { transformTicketValidTime, dateFormat } from '@/utils/format.js'
import { sortBySortIndex } from '@/utils/sort.js'
import { Icon } from 'vant'
import VButton from '@/components/VButton.vue'
import Ticket from '@/components/escheduler/Ticket.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    Ticket,
    VButton
  },
  data () {
    return {
      tickets: [],
      timeList: null,
      popInfo: null
    }
  },
  computed: {
    hasTicket () {
      return this.tickets.length > 0
    },
    chooseTickets () {
      return this.tickets.filter(ticket => ticket.choose)
    },
    disable () {
      return this.chooseTickets.length === 0 || this.chooseTickets.findIndex(ticket => ticket.msg === '-') > -1
    },
    dateTickets () {
      return this.tickets.filter(ticket => !ticket.isUsed && !ticket.origin.fixed)
    },
    canChooseDate () {
      return this.dateTickets.length > 0
    }
  },
  methods: {
    transformTicket (ticket) {
      let limit = ticket.timeList && ticket.timeList.length > 0
      let fixed = limit && ticket.timeType === 2 && ticket.timeList.length === 1 && dateFormat(ticket.timeList[0].startTime, 'yyyy-MM-dd') === dateFormat(ticket.timeList[0].endTime, 'yyyy-MM-dd')

      return {
        origin: {
          id: ticket.userTicketId,
          ref: `ticket${ ticket.userTicketId }`,
          name: ticket.productName,
          tip: ticket.ticketName,
          isValid: ticket.isValid,
          time: transformTicketValidTime(ticket),
          limit: limit,
          fixed: fixed,
          timeType: ticket.timeType,
          timeList: ticket.timeList
        },
        isUsed: ticket.isUsed === 1,
        msg: ticket.isUsed === 1 ? dateFormat(Date.now(), 'yyyy-MM-dd') : fixed ? dateFormat(ticket.timeList[0].startTime, 'yyyy-MM-dd') : '-',
        choose: false
      }
    },
    getTickets () {
      this.$http.post('/api/common/user/ticket/escheduler/list', null, {
        login: true
      }).then(data => {
        let tickets = data.data.sort(sortBySortIndex).map(this.transformTicket)
        if (!this.hasTicket) {
          tickets.forEach(ticket => {
            let curr = this.tickets.find(item => item.origin.id === ticket.origin.id)
            if (curr) {
              ticket.msg = curr.msg
              ticket.choose = curr.choose
            }
          })
        }
        this.tickets = tickets
        this.timeList = data.timeList && data.timeList.length > 0 ? data.timeList : null
      })
    },
    toggle (ticket) {
      ticket.choose = !ticket.choose
    },
    showInfo (ticketOrigin) {
      if (!this.popInfo || this.popInfo.id !== ticketOrigin.id) {
        if (!this.$pageSize) {
          this.$pageSize = document.body.offsetHeight
        }
        let rect = this.$refs[ticketOrigin.ref][0].$el.getBoundingClientRect()
        this.popInfo = {
          id: ticketOrigin.id,
          isTop: rect.top > this.$pageSize - rect.bottom + 200,
          top: `${ rect.top }px`,
          name: ticketOrigin.name,
          tip: ticketOrigin.tip,
          time: ticketOrigin.time
        }
      }
    },
    clean () {
      this.popInfo = null
    },
    toNext () {
      this.$router.push({
        name: 'escheduler-tq'
      })
    }
  },
  created () {
    // let arr = []
    // for (let i = 0; i < 12; i++) {
    //   arr.push(this.transformTicket({
    //     userTicketId: i, // id
    //     ticketId: '111', // id
    //     ticketType: 1, // 123
    //     productName: '', // 
    //     ticketName: '', // otas
    //     personCount: 1, // 
    //     groupId: 1, // id
    //     isActive: 0, // 
    //     isUsed: 0, // 
    //     isValid: 1, // 
    //     timeType: 1, // 12
    //     timeList: [ // 
    //       {
    //         startTime: 1583892000000, // 
    //         endTime: 1593892000000 // 
    //       }
    //     ]
    //   }))
    // }
    // this.tickets = arr
    this.getTickets()
  }
}
</script>

<style lang="scss" scoped>
.c-pop-info {
  position: absolute;
  left: 50%;
  width: 472px;
  padding: 32px;
  border-radius: 16px;
  background-color: $color-white;
  z-index: 99;
  transform: translate(-50%, 60px);

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px;
    font-size: 36px;
    color: $color;
  }

  &.is-top {
    transform: translate(-50%, -100%);
  }
}
</style>
