<template>
  <div class="l-page u-background--blue">
    <v-nav
      :title="$t('common.payment.result.title')"
      icon="cross"
    />
    <div class="l-page__content c-result-wrapper">
      <div
        v-if="order"
        class="c-result has-status"
      >
        <div class="c-result__status">
          <i
            class="c-result__icon"
            :class="statusClass"
          ></i>
        </div>
        <div class="c-result__header">
          {{ statusMsg }}
          <div
            v-if="tipMsg"
            class="is-tip--dark u-font u-pre-wrap u-separate"
          >
            {{ tipMsg }}
          </div>
          <div
            v-if="status === 36"
            class="o-button is-trigger is-auto u-separate--near"
          >
            {{ $t('common.payment.result.btn.36') }}
          </div>
        </div>
        <div class="c-result__content">
          <order
            class="c-vlist-item"
            :order="order"
          />
          <template v-if="status === 1">
            <div class="c-vlist-item">
              <router-link
                tag="div"
                class="o-button is-trigger is-auto"
                :to="{ name: 'payment', params: { id: orderId } }"
                replace
              >
                {{ $t('common.payment.result.btn.1') }}
              </router-link>
            </div>
          </template>
          <template v-if="canViewDetail">
            <div class="c-vlist-item">
              <router-link
                tag="div"
                class="o-button is-trigger is-auto"
                :class="{ 'is-hollow': status === 36 }"
                :to="{ name: 'order-detail', params: { id: orderId } }"
              >
                {{ $t('common.payment.result.detail') }}
              </router-link>
            </div>
          </template>
          <template v-if="status >= 41">
            <div
              v-if="canEschedule"
              class="c-vlist-item"
            >
              <router-link
                tag="div"
                class="o-button is-trigger is-auto"
                :to="{ name: 'escheduler' }"
                replace
              >
                {{ $t('common.payment.result.escheduler') }}
              </router-link>
            </div>
            <div
              v-if="canShare"
              class="c-vlist-item"
            >
              <router-link
                tag="div"
                class="o-button is-trigger is-hollow is-auto"
                :to="{ name: 'mine' }"
                replace
              >
                {{ $t('common.payment.result.send') }}
              </router-link>
            </div>
            <div class="c-vlist-item u-wrapper u-text-center">
              <router-link
                tag="div"
                class="u-inline-flex"
                :to="{ name: 'mine' }"
                replace
              >
                <div class="is-sub--blue">{{ $t('common.payment.result.view') }}</div>
                <img class="o-icon" src="@/assets/icon-arrow--right--blue.png"/>
              </router-link>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { transformTicketValidTime } from '@/utils/format.js'
import { Icon } from 'vant'
import Order from '@/components/ticket/Order.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    Order
  },
  data () {
    return {
      status: 0,
      orderId: null,
      order: null,
      canEschedule: false,
      canShare: false
    }
  },
  computed: {
    canViewDetail () {
      switch (this.status) {
        case 1:
        case 2:
        case 6:
          return false
        case 36:
          return true
        default:
          return this.status < 41
      }
    },
    statusClass () {
      switch (this.status) {
        case 1:
        case 2:
        case 6:
        case 36:
          return 'is-wran'
        default:
          return this.status >= 41 ? 'is-success' : 'is-processing'
      }
    },
    statusMsg () {
      switch (this.status) {
        case 1:
        case 2:
        case 6:
        case 36:
          return this.$t(`common.payment.result.order.${ this.status }`)
        default:
          return this.status >= 41 ? this.$t('common.payment.result.order.41') : this.$t('common.payment.result.order.0')
      }
    },
    tipMsg () {
      switch (this.status) {
        case 1:
        case 2:
        case 6:
          return ''
        case 36:
          return this.$t(`common.payment.result.tip.${ this.status }`)
        default:
          return this.status >= 41 ? '' : this.$t('common.payment.result.tip.0')
      }
    }
  },
  methods: {
    fetch () {
      clearTimeout(this.$timer)
      this.$http.post('/api/common/user/ticket/order/status', {
        orderId: this.orderId
      }, {
        login: true,
        loading: this.order === null,
        server: this.order === null
      }).then(data => {
        let { status } = data

        if (this.status === -1) {
          let { tickets, canEscheduler, canShare, discountPrice, coupon, totalPrice } = data

          this.canEschedule = canEscheduler
          this.canShare = canShare

          this.order = {
            list: tickets.map(ticket => {
              return {
                name: ticket.ticketName,
                count: ticket.ticketCount,
                total: ticket.price,
                date: transformTicketValidTime(ticket)
              }
            }),
            subtotal: coupon ? totalPrice : null,
            discount: coupon ? coupon.discount ? `- ${ coupon.discount }` : coupon.name : '',
            total: coupon ? discountPrice : totalPrice
          }
        }

        // 
        if (status === 1 && this.$payed) {
          status = 0
        }

        if (this.status !== status) {
          this.status = status
        }

        if (this.status < 41 && this.status !== 1 && this.status !== 2 && this.status !== 6 && this.status !== 36) {
          this.$timer = setTimeout(() => {
            this.fetch()
          }, 2000)
        }
      }, err => {
        if (err.system && !err.abort) {
          this.fetch()
        }
      })
    }
  },
  created () {
    this.$timer = -1
    this.$payed = this.$route.query.status === 'payed'
    this.orderId = this.$route.params.id
    this.fetch()
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.c-result {
  /deep/ .c-order {
    text-align: left;
    padding: 0;

    &__title {
      text-align: center;
    }
  }

  /deep/ .l-collection__item {
    padding: 24px 0;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
    }
  }
}
</style>
