<template>
  <div class="l-page" @touchmove="clean" @click="clean">
    <v-nav :title="$t('escheduler.cdate.title')"/>
    <div class="l-page__content l-flex--column">
      <div class="c-escheduler-ticket-list l-flex__none u-relative">
        <div class="c-escheduler-ticket-list__title is-tip--dark">{{ $t('escheduler.cticket') }}</div>
        <div class="l-hlist l-flex__none">
          <div class="c-escheduler-ticket-list__content l-hlist__wrapper">
            <ticket
              v-for="ticket in tickets"
              :key="ticket.origin.id"
              class="c-escheduler-ticket-list__item"
              :ticket="ticket"
              @click.native="toggle(ticket)"
              @info="showInfo"
            />
          </div>
        </div>
        <div
          v-if="popInfo"
          class="c-pop-info has-shadow"
          @click.stop
          @touchmove.stop
        >
          <van-icon
            class="c-pop-info__close"
            name="cross"
            @click="clean"
          />
          <div class="is-content--dark">{{ popInfo.name }}</div>
          <div class="is-tip--dark u-font u-separate--nearest">{{ popInfo.tip }}</div>
          <div class="is-tip u-separate--nearest">{{ popInfo.time }}</div>
        </div>
      </div>
      <div class="l-flex__fill u-scroll-y">
        <div class="has-separate--t">
          <calendar
            v-if="count > 0"
            :ranges="ranges"
            :day="dateTime"
            mini
            @change="onCalendarChange"
          />
        </div>
      </div>
      <div class="c-fixed-footer has-hairline--tb">
        <v-button
          class="l-flex__fill"
          :text="$t('btn.confirmupper')"
          :disable="disable"
          small
          @click="toSave"
        />
      </div>
    </div>
    <van-dialog
      className="c-dialog"
      v-model="dialogMsg.show"
      :title="$t('modal.title')"
      :message="$t('escheduler.cdate.warn')"
      :confirm-button-text="$t('btn.known')"
    />
  </div>
</template>

<script>
import { dateFormat } from '@/utils/format.js'
import DialogMixin from '@/components/mixins/dialog.js'
import { Icon } from 'vant'
import Calendar from '@/components/Calendar.vue'
import VButton from '@/components/VButton.vue'
import Ticket from '@/components/escheduler/Ticket.vue'

export default {
  mixins: [ DialogMixin ],
  components: {
    [ Icon.name ]: Icon,
    Calendar,
    Ticket,
    VButton
  },
  props: {
    dateTickets: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      dateTime: '',
      ranges: [],
      tickets: [],
      popInfo: null
    }
  },
  computed: {
    chooseTickets () {
      return this.tickets.filter(ticket => ticket.choose)
    },
    count () {
      return this.chooseTickets.length
    },
    disable () {
      return this.count === 0 || this.dateTime === ''
    }
  },
  methods: {
    transformTicket ({ origin }) {
      return {
        origin,
        msg: origin.time,
        choose: false
      }
    },
    showInfo (ticketOrigin) {
      if (!this.popInfo || this.popInfo.id !== ticketOrigin.id) {
        this.popInfo = {
          id: ticketOrigin.id,
          name: ticketOrigin.name,
          tip: ticketOrigin.tip,
          time: ticketOrigin.time
        }
      }
    },
    clean () {
      this.popInfo = null
    },
    onCalendarChange (date) {
      this.dateTime = date
    },
    addTicket (ticket) {
      let endTime = ticket.origin.timeList[0].endTime

      // 
      if (endTime >= this.ranges[this.ranges.length - 1].endTime) {
        return true
      }

      // 
      if (endTime < this.ranges[0].startTime) {
        return false
      }

      // 
      this.ranges = this.ranges.filter(time => time.startTime <= endTime).map(time => {
        return {
          startTime: time.startTime,
          endTime: Math.min(time.endTime, endTime)
        }
      })

      return true
    },
    filter (list, minDateTime, maxDateTime) {
      return list.filter(time => !(time.startTime > maxDateTime || time.endTime < minDateTime)).map(time => {
        return {
          startTime: Math.max(time.startTime, minDateTime),
          endTime: Math.min(time.endTime, maxDateTime)
        }
      })
    },
    isCoincident (a, b) {
      return !(a.startTime > b.endTime || a.endTime < b.startTime)
    },
    addFixedTicket (ticket) {
      let timeList = ticket.origin.timeList
      let ticketMinDateTime = timeList[0].startTime
      let ticketMaxDateTime = timeList[timeList.length - 1].endTime
      let currMinDateTime = this.ranges[0].startTime
      let currMaxDateTime = this.ranges[this.ranges.length - 1].endTime

      // 
      if (ticketMinDateTime > currMaxDateTime) {
        return false
      }

      // 
      if (ticketMaxDateTime < currMinDateTime) {
        return false
      }

      let minDateTime = Math.max(ticketMinDateTime, currMinDateTime)
      let maxDateTime = Math.min(ticketMaxDateTime, currMaxDateTime)
      let list = this.filter(timeList, minDateTime, maxDateTime)
      let ranges = this.filter(this.ranges, minDateTime, maxDateTime)

      let arr = []
      let index = 0
      // 
      for (let i = 0, j = list.length; i < j; i++) {
        while (index < ranges.length) {
          let startTime = ranges[index].startTime
          let endTime = ranges[index].endTime

          // 
          if (this.isCoincident(list[i], ranges[index])) {
            arr.push({
              startTime: Math.max(list[i].startTime, startTime),
              endTime: Math.min(list[i].endTime, endTime)
            })
          }

          // 
          // 
          if (list[i].endTime <= endTime) {
            break
          }

          index += 1
        }
      }

      this.ranges = arr

      return true
    },
    minusTicket () {
      let tickets = this.chooseTickets.filter(ticket => ticket.origin.timeType === 1)
      let index = -1
      for (let i = 0, j = tickets.length; i < j; i++) {
        if (index === -1 || tickets[i].origin.timeList[0].endTime < tickets[index].origin.timeList[0].endTime) {
          index = i
        }
      }
      let dateTicket = index >= 0 ? tickets[index] : null

      let fixedTickets = this.chooseTickets.filter(ticket => ticket.origin.timeType === 2)

      if (fixedTickets.length > 0) {
        for (let i = 0, j = fixedTickets.length; i < j; i++) {
          if (i === 0) {
            this.addFirstTicket(fixedTickets[i])
          } else {
            this.addFixedTicket(fixedTickets[i])
          }
        }
        dateTicket && this.addTicket(dateTicket)
      } else {
        this.addFirstTicket(dateTicket)
      }
    },
    checkDateTime () {
      // 
      if (this.dateTime) {
        if (this.ranges.length > 0) {
          let dateTime = new Date(this.dateTime.replace(/-/g, '/')).getTime()
          if (this.ranges.findIndex(time => time.startTime <= dateTime && time.endTime >= dateTime) === -1) {
            this.dateTime = ''
          }
        } else {
          this.dateTime = ''
        }
      }
    },
    addFirstTicket (ticket) {
      if (ticket.origin.limit) {
        this.ranges = ticket.origin.timeList.map(time => {
          return {
            startTime: time.startTime ? Math.max(time.startTime, this.$tomorrow) : this.$tomorrow,
            endTime: time.endTime
          }
        })
      } else {
        this.ranges = [{
          startTime: this.$tomorrow,
          endTime: this.$tomorrow + 10 * 365 * 24 * 3600 * 1000
        }]
      }
    },
    toggle (ticket) {
      if (ticket.choose) {
        ticket.choose = false
        if (this.count === 0) {
          this.ranges = []
          this.dateTime = ''
        } else {
          if (!ticket.origin.limit || ticket.origin.timeType === 1 && ticket.origin.timeList[0].endTime > this.ranges[this.ranges.length - 1].endTime) {
            return
          }
          this.minusTicket()
          this.checkDateTime()
        }
      } else {
        if (this.count === 0) {
          this.$tomorrow = new Date(dateFormat(Date.now() + 24 * 3600 * 1000, 'yyyy/MM/dd')).getTime()
        }
        if (ticket.origin.limit) {
          let ranges = ticket.origin.timeList.filter(time => time.endTime >= this.$tomorrow)
          if (ranges.length === 0) {
            return false
          }
        }
        if (this.ranges.length === 0) {
          this.addFirstTicket(ticket)
        } else {
          if (ticket.origin.limit) {
            let hasAdd = ticket.origin.timeType === 1 ? this.addTicket(ticket) : this.addFixedTicket(ticket)
            if (!hasAdd) {
              this.dialogMsg.show = true
              return
            }
            this.checkDateTime()
          }
        }
        ticket.choose = true
      }
    },
    toSave () {
      this.chooseTickets.forEach(ticket => {
        let esTicket = this.dateTickets.find(item => item.origin.id === ticket.origin.id)
        console.log(esTicket)
        if (esTicket) {
          esTicket.msg = this.dateTime
        }
      })
      this.$features.back()
    }
  },
  created () {
    this.tickets = this.dateTickets.map(this.transformTicket)
    this.toggle(this.tickets[0])
  }
}
</script>

<style lang="scss" scoped>
.c-escheduler-ticket-list {
  &__title {
    padding: 40px 40px 0;
  }

  &__content {
    padding: 32px 40px 40px;
  }

  &__item {
    width: 326px;

    & + & {
      margin-left: 20px;
    }
  }
}

.c-pop-info {
  position: absolute;
  top: 160px;
  left: 50%;
  width: 472px;
  padding: 32px;
  border-radius: 16px;
  background-color: $color-white;
  z-index: 99;
  transform: translate(-50%, 0);

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px;
    font-size: 36px;
    color: $color;
  }
}
</style>
