<template>
  <div
    class="o-cell--panel has-hairline--b l-flex--row"
    @click="onClick"
  >
    <div class="is-content--dark l-flex__fill">
      <template v-if="value">
        <div class="o-cell--panel__title is-ellipsis u-color">{{ title }}</div>
        <div
          class="is-ellipsis"
          :class="{ 'u-color': disable }"
        >
          {{ value }}
        </div>
      </template>
      <div
        v-else
        class="u-font--bold"
      >
        {{ placeholder }}
      </div>
    </div>
    <img
      v-if="!disable"
      class="o-cell--panel__icon l-flex__none"
      src="@/assets/icon-arrow--right.png"
    />
  </div>
</template>

<script>
export default {
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    disable: {
      type: [Boolean, String],
      default: false
    }
  },
  methods: {
    onClick () {
      if (!this.disable) {
        this.$emit('click')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.o-cell--panel {
  box-sizing: border-box;
  position: relative;
  height: 120px;

  &__title {
    font-size: 24px;
  }

  &__icon {
    width: 32px;
    height: 32px;
  }
}
</style>
